  {
    title: '走进八一起义纪念馆，传承革命精神',
    content: '<p>7月26日，中国矿业大学计算机科学与技术学院“e新向党”团队成员赴南昌市西湖区八一起义纪念馆开展暑期社会实践调研。</p><p>据纪念馆工作人员介绍，1927年8月1日，在以周恩来为书记的前敌委员会的领导下，贺龙、叶挺、朱德、刘伯承等人率领党所掌握和影响下的军队两万多人，在南昌城头打响了武装反抗国民党反动派的第一枪。经过4个多小时激烈战斗，起义军歼敌3000余人，缴获机枪800余挺、步枪5000余支、子弹70万余发。南昌八一起义纪念馆的建立就是为了纪念这一伟大的历史事件。</p><p>在纪念馆中，实践团成员与工作人员深入交流，仿佛置身于那段激情燃烧的岁月。看着一件件珍贵的文物，听着一个个动人的故事，深刻感受到了革命先辈们的英勇无畏。</p><p>正如习近平总书记所说：“要把红色基因传承好，确保红色江山永不变色”，我们也应铭记历史，传承和弘扬伟大的革命精神，让其在新时代绽放更加耀眼的光芒。在今后的学习和生活中，我们要以革命先辈为榜样，坚定理想信念，勇于拼搏奋斗，为实现中华民族伟大复兴的中国梦贡献自己的力量。</p>',
    images: [
      { src: '八一起义纪念馆1.png', description: '在八一起义纪念馆内观看《中国梦 强国梦》讲解视频' },
      { src: '八一起义纪念馆2.png', description: '参观八一南昌起义总指挥部旧址' }
    ],
    source: '吴云岩',
    photographer: '吴云岩',
    editor: '红色编辑3',
    reviewer: '红色审核3'
  },
  {
    title: '重温西安事变，传承爱国精神',
    content: '<p>8月8日，中国矿业大学中国矿业大学计算机科学与技术学院“e新向党”团队成员赴陕西省西安市西安事变纪念馆开展暑期社会实践调研。</p><p>西安事变纪念馆张学良公馆位于西安市建国路69号，是在西安事变的重要旧址张学良将军公馆的专题性纪念馆。</p><p>1936年12月12日，为挽救民族危亡、劝谏蒋介石改变“攘外必先安内”的既定国策、停止内战一致抗日，张学良、杨虎城毅然在临潼对蒋介石实行“兵谏”，扣留来陕督战的蒋介石，发动了震惊中外的“西安事变”，亦称双十二事变。提出抗日救国八项政治主张，逼蒋介石抗日。1936年12月25日，在中共中央和周恩来等人的努力下，蒋介石接受“停止内战、联共抗日”等六项主张，为“西安事变”的和平解决奠定了基础。</p><p>与工作人员交流得知，本纪念馆占地9141平方米，展览面积6598平方米，馆藏文物近300件（组），其中一级文物26件组。并且以西安事变为主线，将旧址复原陈列和西安事变史实陈列、张学良、杨虎城两将军的生平事迹陈列相结合。</p><p>本次实践，不仅再次重温了红色革命道路上的重要历史转折点，更被张学良等识大局的爱国志士的爱国情怀和牺牲精神所感染。作为新时代的青年，我们更应该铭记历史，珍惜当下，为中华民族伟大复兴贡献力量。</p>',
    images: [
      { src: '西安事变张学良纪念馆1.png', description: '实践成员参观照' },
      { src: '西安事变张学良纪念馆2.png', description: '实践成员阅读爱国志士的请愿联合书' }
    ],
    source: '冯圣滢',
    photographer: '冯圣滢',
    editor: '红色编辑4',
    reviewer: '红色审核4'
  },
  {
    title: '参观杨虎城别墅，重温西安事变历史',
    content: '<p>7月30日，到西安事变旧址杨虎城别墅进行参观。在参观的过程中，首先看到了杨虎城将军的住宅，其次通过对各种所展示的文物的浏览，了解到了西安事变发生的历史背景和环境，也大致了解了西安事变的起因、经过和结果。通过本次参观活动，不仅可以了解这位历史任务的生活和历史背景，更能感受到那段波澜壮阔的革命岁月中英勇与坚定。</p><p>我们更应该珍惜这份历史遗迹，传承和发扬先辈们的革命精神。通过讲解员的介绍，我了解到西安事变是1936年发生的一次重要历史事件，它标志着国共两党合作抗日的开始，对中国近代史产生了深远的影响。参观了杨虎城将军的故居，通过展品和资料，我对杨虎城将军的生平和他在西安事变中的关键作用有了更深入的了解。站在杨虎城将军的故居前，我仿佛能感受到那个时代的氛围，对杨虎城将军的勇敢和智慧表示深深的敬意。</p><p>这次参观让我深刻认识到历史的重要性，历史不仅是过去的记忆，更是对未来的启示。杨虎城将军和其他革命先烈的牺牲精神让我深受感动，他们为了国家和民族的利益不惜一切代价。西安事变的和平解决展示了和平解决争端的重要性，这对于我们今天处理国际关系和冲突有着重要的启示。</p><p>我计划继续深入学习中国近现代史，特别是抗日战争时期的历史。将这次参观的感悟分享给家人和朋友，让更多的人了解和传承这段历史。在日常生活中，我将努力促进和平与理解，为构建和谐社会贡献自己的力量。通过这次参观，我对西安事变和杨虎城将军有了更加深刻的认识。这次经历不仅增长了我的历史知识，也激发了我对历史的兴趣和对和平的珍视。</p>',
