  {
    title: '瞻仰成县烈士陵园，缅怀先烈传承红色精神',
    content: '<p>2024年8月7日，为了发扬红色精神，传承优秀文化，团队成员武纪元来到成县革命烈士陵园进行瞻仰学习。</p><p>成县革命烈士陵园位于成县城郊西北山塬上，占地面积4809平方米，园内修建了革命烈士纪念碑一座，碑高12.8米，正面刻有“革命烈士永垂不朽”八个大字；背面刻有对革命先烈表示怀念的碑文。修建革命烈士纪念馆一座，馆内陈列了烈士事迹资料及部分烈士遗物。</p><p>成县革命烈士陵园是为了纪念在“成徽两康战役”中牺牲的革命烈士。1936年，中国工农红军第二方面军于9月17日攻占成县县城，随后在县城西面与国民党第三军王均部展开激战。在同敌人的激烈战斗中，红二方面军四名团级干部和许多红军战士壮烈牺牲。</p><p>岁月转瞬，历史成碑。烈士陵园中十分安静整洁，令人难以想象这里曾经是发生过激烈战斗的战场。昔日的战火硝烟已经远去，但是先辈们的伟大精神却仍然留在此地，并不断鼓舞无数的后来者继续坚持奋斗，星火相承。</p><p>“为有牺牲多壮志，敢教日月换新天”。正是因为革命先辈舍生忘死，前仆后继的战斗，才有了如今的新中国。为了不辜负先辈们所寄予的厚望，我们青年人更应当热血不改，为了中华民族伟大复兴而不懈奋斗！</p>',
    images: [
      { src: '成县1.png', description: '成县革命烈士事迹陈列馆外部' },
      { src: '成县2.png', description: '陈列馆内部分文物展览' }
    ],
