  {
    title: '重温八路军驻甘办事处历史，感悟革命精神',
    content: '<p>兰州八路军办事处纪念馆位于酒泉路互助巷2号，是一座典型的兰州传统民居风格的四合院建筑。这座位于兰州市城关区的纪念馆，不仅是甘肃省唯一家抗战纪念馆，更是全国重要的红色教育基地。其中，酒泉路旧址展出了“热血陇原—八路军驻甘办事处与甘肃抗日救亡展”和“八路军驻甘办事处原状陈列展”两大主题展览，通过大量历史图片、文物和文献，生动再现了八路军驻甘办事处在抗战期间的重要贡献。</p><p>从办事处成立的历史背景，到组织和推动甘肃抗日救亡运动的发展;从积极营救和收容失散在甘青带的西路军将士，到输送大批进步青年奔赴延安参加革命；从协助和指导甘肃工委扩大党的地下组织，到承担党的干部往返苏联和新疆的接待任务和抗日支前物资的转运工作......每一处展览都让人感受到革命先辈们不畏艰难、勇于斗争的伟大精神。</p><p>“热血陇原—八路军驻甘办事处与甘肃抗日救亡展”这个展览通过丰富的图文资料和实物展示，让我们了解到八路军驻甘办事处在短短六年时间里，如何在极端艰难困苦的条件下，坚持斗争，出色完成了党交给的各项任务。其中，最让我印象深刻的是关于营救和收容西路军将士的展览部分。据史料记载，办事处成功营救和收容了数千名失散在甘青一带的西路军将士，为他们的生存和后续革命斗争提供了有力保障。</p><p>“八路军驻甘办事处原状陈列展”这个展览以原状房屋为基础，通过精心设计的图片、文字说明牌和74件文物，生动还原了办事处当年的工作和生活场景。我们仿佛穿越时空，回到了那个战火纷飞的年代，感受到了革命先辈们的艰辛与不易。</p>',
    images: [
      { src: '兰州铁道馆1.png', description: '纪念馆部分单元内容展示' },
      { src: '兰州铁道馆2.png', description: '实践组成员观摩纪念馆' }
    ],
