  {
    title: '走进枣园革命旧址，传承延安精神',
    content: '<p>7月27日下午及7月28日上午，实践组成员付焱灼同学前往枣园革命旧址开展社会活动。</p><p>在参观过程中，成员深入了解到了毛泽东、周恩来等老一辈无产阶级革命家日常生活的点点滴滴，瞻仰了革命先烈的光荣事迹，回顾了当时发生的重要历史事件。破旧的木凳，昏暗的窑洞，无不诉说着当时条件的艰苦，一件件珍贵不已的革命文物，一段段掷地有声的文字，让队员更加清晰地了解了什么是延安精神，什么是红色基因；一桩桩栩栩如生的情景再现，一张张记录历史的老照片，让成员更加深刻坚定了艰苦奋斗，光明在前的决心。</p><p>毛泽东主席的故居简单而朴素，书桌上的油灯，墙上的地图，都透露出那个时代革命者的勤劳与智慧。学习到了什么叫做“实事求是”，什么叫做“团结协作”，什么叫做“为人民服务”。这些宝贵的精神财富，对我们今天的工作和生活有着重要的指导意义。</p><p>正如习近平总书记所强调：“要深入实施红色基因传承工程，大力弘扬以爱国主义为核心的民族精神、以改革创新为核心的时代精神，不断增强对中华民族的认同感和自豪感，振奋各族人民奋进新征程、建功新时代的精气神。”</p><p>在今后的学习和生活中，我们要不断回望历史岁月长河，以革命先辈为榜样，坚定自我理想信念，为实现中华民族伟大复兴贡献出自己的青春力量。</p>',
    images: [
      { src: '枣园1.png', description: '实践组成员与领导人雕塑合影' },
      { src: '枣园2.png', description: '实践组成员与中央大礼堂合影' }
    ],
    source: '付焱灼',
    photographer: '付焱灼',
    editor: '红色编辑11',
    reviewer: '红色审核11'
  },
  {
    title: '采访抗美援朝老兵，传承伟大爱国精神',
    content: '<p>为了传承抗美援朝精神，导觅甘肃红色文化，2024年8月10日，团队成员王怡如、史锦博、王德园前往甘肃省兰州市七里河西津桥社区采访抗美援朝战士胡成玉。</p><p>在采访开始前，值得注意的是胡成玉穿上了当时抗美援朝的军装，整理一番才坐下为我们讲述抗美援朝的过程。</p><p>距离抗美援朝战争过去了74年，谈及当年的峥嵘岁月时，胡成玉仍精神抖擞、精力充沛。他回忆道，他于14岁时参军,于1950年参加抗美援朝成为部队中的卫生员。在介绍抗美援朝的历史时,胡成玉还拿出地图为我们展示当时的行军路线。“雄赳赳，气昂昂，跨过鸭绿江，保和平，卫祖国，就是保家乡......”讲到激动处，胡成玉唱起了这首振奋人心的中国人民志愿军战歌，在场的实践团成员无不动容。透过铿锵的歌声，实践团成员仿佛看到了在那段战火纷飞的岁月，革命前辈们用小米加步枪转战局奋勇杀敌的场景。</p><p>胡成玉回忆当时的条件很艰苦，天寒地冻，将士们穿不暖吃不饱。尽管如此，在如此恶劣的条件下，将士们仍坚守着，为国家而奉献自己。胡成玉由衷感慨道：“我经历过新旧社会的变迁，能够深刻感受到中国共产党的伟大。我从不后悔将我的青春奉献给祖国，我愿意为共产主义事业奋斗终身。我现在能够健康地活到晚年，每个月有国家给我发的钱，儿孙满堂，现在的生活很甜！”</p><p>本次实地调研采访让团队成员们感受到了先辈的不易与伟大精神，这让我们可以更好地弘扬爱国主义精神，团结协作，勇敢顽强，为实现中华民族伟大复兴的中国梦而努力奋斗。</p>',
    images: [
      { src: '七里河西津桥1.png', description: '实践组成员倾听胡成玉先生讲述革命故事' },
      { src: '七里河西津桥2.png', description: '实践组成员与胡成玉先生合影留念' }
    ],
   /*  video: 'video-02.mp4', */
