    source: '红色故事来源12',
    photographer: '红色摄影师12',
    editor: '红色编辑12',
    reviewer: '红色审核12'
  },


  {
    title: '探访会宁红军长征纪念馆，传承长征精神',
    content: '<p>为了感受先烈英勇事迹，寻觅甘肃红色基因，2024年8月10日，团队成员赵明与南宇涵前往甘肃省白银市会宁县红军长征纪念馆进行实地调研。</p><p>会宁红军长征胜利纪念馆是一座以纪念红军三大主力在会宁胜利会师、长征胜利结束这一伟大历史事件为主题的专题性纪念馆，它也是全国仅有的一座全面反映红军长征历史的纪念馆。这里记录着红军不畏艰难险阻，勇往直前的辉煌历史。</p><p>团队成员先后参观了红军长征胜利纪念馆、瞻仰了中国工农红军第一二四方面军会师纪念塔和会师楼，通过讲解员详细的讲解和馆内陈列的珍贵文物、图片、影像资料等，全面细致地了解了红军长征的不易，也感受到了长征精神的伟大力量。</p><p>本次实地调研让团队成员们感受到了历史的厚重与红色文化的魅力，坚定了大家立志报国、投身祖国建设的使命感与责任感。而同学们也会坚定理想信念，为实现中华民族伟大复兴的中国梦贡献自己的力量。</p>',
    images: [
      { src: '会宁红军纪念馆1.png', description: '团队成员参观会宁红军长征纪念馆' },
      { src: '会宁红军纪念馆2.png', description: '红军长征会师门' }
    ],
    source: '赵明 南宇涵',
    photographer: '赵明 南宇涵',
    editor: '红色编辑12',
    reviewer: '红色审核12'
