  {
    title: '台儿庄战役纪念馆参观，铭记抗战胜利精神',
    content: '<p>7月13日，红色攻城狮-铜芯协力实践团实践组成员倪浩宸同学前往台儿庄大战纪念馆开展社会实践活动。在参观过程中，队员了解了台儿庄大捷中涌现出的先进人物及其光荣事迹，学习了台儿庄大战的来龙去脉，瞻仰了革命文物和烈士遗迹。</p><p>台儿庄大捷，又称台儿庄战役、鲁南会战或血战台儿庄。台儿庄战役的起止时间有几种说法，一般认为从1938年3月16日开始至4月15日结束。战役由滕县战斗、临沂附近战斗、台儿庄战斗和日军的溃退，中国军队的追击作战等部分组成。它打击了日本侵略者的嚣张气焰，坚定了全国军民坚持抗战的信心。这次战役鼓舞了全民族的士气，改变了国际视听，消灭了日本侵略者的威风，歼灭了日军大量有生力量。此次大捷是中华民族全面抗战以来，继长城战役、平型关大捷等战役后，中国人民取得的又一次胜利，是抗日战争以来取得的最大胜利，也是徐州会战中国革命军取得的一次重大胜利。</p><p>一件件文物、一幅幅地图、一段段文字，无不诉说着无数革命先辈的伟大，令人不禁驻足。这次活动让实践队员在亲身感受战争氛围的同时涵养了爱国精神，加深了其对脚下土地来之不易的认识，更坚定了队员接续奋斗，实现中华民族伟大复兴中国梦的决心。</p>',
    images: [
      { src: '台儿庄战役纪念馆1.png', description: '台儿庄大战纪念馆正门' },
      { src: '台儿庄战役纪念馆2.png', description: '台儿庄战役大事记' }
    ],
   /*  video: 'video-02.mp4', */
    videodescription: '重庆谈判的影像资料',
    source: '倪浩宸',
    photographer: '倪浩宸',
    editor: '红色编辑12',
    reviewer: '红色审核12'
  },
  {
    title: '参观济南战役纪念馆，感悟革命精神力量',
    content: '<p>7月30日上午，实践组成员田吉运同学前往济南战役纪念馆开展社会实践活动。这座纪念馆犹如一座历史的丰碑，向人们诉说着那段血与火的岁月。</p><p>展馆一楼包含“和战博弈 生死较量”“运筹帷幄 剑指济南”“厉兵秣马 蓄势待发”等部分，展示了济南战役的背景、战略决策以及战前的准备工作等，保留毛主席手稿以及当时的作战武器和用品。</p><p>二楼包含“八天八夜 鏖战古城”、“战略决战 凯歌行进”、“接管济南 泉城新生”、“英魂长眠 日月同辉”、“新时代 新泉城”等展区，着重介绍济南战役的具体战斗过程、激烈的战斗场景，以及一些英雄烈士的事迹。</p><p>通过参观，实践组成员更全面地了解了济南战役的全貌。战士们前赴后继、冲锋陷阵的身影仿佛就在其眼前，他们为了济南的解放和人民的幸福，谱写了壮丽的英雄赞歌。无数革命先烈舍生忘死，为了理想和信念不惜牺牲一切，他们的精神令人敬仰，激励着后人不断奋进。</p><p>在纪念馆中，实践组成员深刻体会到今天的和平与繁荣来之不易，那是无数先烈用生命换来的宝贵财富，从而更加清醒地认识到自己肩负的责任和使命。</p><p>济南战役诞生了“爱党爱军，依靠人民，英勇顽强，敢于胜利”的济南战役精神。这种精神是中华民族红色基因的重要组成部分，它已深深融入济南人民',
    images: [
