    reviewer: '红色审核12'
  },
  {
    title: '走访毛泽东与第一师范纪念馆，感悟革命精神传承',
    content: '<p>2024年8月20日星期二，中国矿业大学计算机科学与技术学院“e新向党”团队成员赴毛泽东与第一师范纪念馆，进行探寻爱国主义教育，传承红色基因等相关活动。</p><p>在调研过程中，实践团成员通过参观展厅对湖南第一师范及青年毛泽东的关系有了深入了解。第一展厅“源远流长的中华名校”详细介绍了湖南第一师范的历史渊源，从1161年的城南书院到1903年成立的湖南师范馆，展现了其悠久的教育传统和办学特色。展览强调了学校从书院教育到师范教育的转变，以及它如何成为湖南乃至全国知名的教育机构，培养了毛泽东等一大批杰出人物。</p><p>第二至第五展厅以主题展览“一代伟人从这里起步”为核心，全面展示了青年毛泽东在湖南第一师范的成长历程。第二展厅介绍了毛泽东的求学经历，包括他从乡关走出的决心和在师范期间的努力学习，如何在全校名列前茅，为他日后的革命事业奠定了坚实基础。第三展厅则重点介绍了毛泽东在求学期间如何践行“文明其精神、野蛮其体魄”的理念，探索人生真理并重视身体锻炼，以备战斗生涯。第四展厅展示了毛泽东与名师的互动和学友的砥砺，讲述了他在新民学会的成立、湖南五四运动中的领导角色，以及在附小工作的经历。第五展厅则涵盖了毛泽东确立马克思主义信仰的过程，以及他如何由学生转变为革命家，同时展示了湖南第一师范如何继续发扬毛泽东所倡导的革命精神和优良教育传统。</p><p>整个展览不仅突出了毛泽东与湖南第一师范的深厚联系，还深刻揭示了这段经历对他革命生涯的决定性影响。</p><p>习近平总书记在参观青年毛泽东主题展馆时曾说：“国家要强大，必须办好教育。一师是开展爱国主义教育、传承红色基因的好地方，要把这一红色资源保护运用好。学校要立德树人，教师要当好大先生，不仅要注重提高学生知识文化素养，更要上好思政课，教育引导学生明德知耻，树牢社会主义核心价值观，立报国强国大志向，努力成为堪当强国建设、民族复兴大任的栋梁之材。”</p><p>通过此次社会实践活动，实践团成员深入学习了湖南第一师范的历史背景及青年毛泽东的成长历程，并切实体悟了湖南第一师范在中国现代教育史中的重要地位。此次实践不仅增强了实践团成员对教育历史和人物成长的了解，也深化了对红色教育精神的认识。实践团成员通过亲身参观和学习，感受到了毛泽东在湖南第一师范时期的理想信念和成长历程，明白了红色教育对中国革命及现代教育发展的深远影响。这一经历使我们更加明确了教育在社会变革中的重要作用，激励我们将红色精神融入到当代青年的学习与实践中，为新时代的中国发展贡献青春力量！</p>',
    images: [
      { src: '第一师范纪念馆1.png', description: '实践团成员参观纪念馆旧物' },
      { src: '第一师范纪念馆2.png', description: '毛泽东“野蛮其体魄”' }
    ],
   /*  video: 'video-02.mp4', */
    videodescription: '重庆谈判的影像资料',
    source: '蒋云辉',
    photographer: '蒋云辉',
    editor: '红色编辑12',
    reviewer: '红色审核12'
  },
  {
    title: '重温东北抗联精神，砥砺前行传承红色基因',
    content: '<p>习总书记曾言：“一个有希望的民族不能没有英雄，一个有前途的国家不能没有先锋。”为寻红色印记，随总书记步伐，2024年7月22日，“铜芯协力”实践团前往东北抗联纪念馆开展社会实践调研，深筑信仰根基。</p><p>在东北抗日联军纪念馆内，实践团成员深入了解了东北抗日联军的英雄事迹，尤其是杨靖宇和赵一曼等民族英雄；观看了展馆内陈列的有关抗日战争史的多项展品与烈士雕像；瞻仰了周恩来总理的名言“革命先烈永垂不朽”以及中国共产党入党誓词；回顾了东北军民十四年艰苦卓绝的抗战历程。伫立在东北抗联烈士名录前，静静聆听烈士们的故事，每一个深深镌刻的名字背后都是对党和国家的无限忠诚。</p><p>“天地英雄气，千秋尚凛然。”无数英雄先烈是我们民族的脊梁，是我们不断开拓前进的勇气和力量所在。建党103周年之际，实践团重走抗联路、传承英雄志，让伟大东北抗联精神生生不息、薪火相传。将东北抗联精神和杨靖宇精神转化为坚定信念，传承红色基因，发扬革命传统，勇担时代责任，在祖国蓬勃发展的新征程中，砥砺奋进、勇毅前行。</p>',
    images: [
      { src: '东北抗联1.png', description: '实践组成员与东北烈士事业管理委员会合影' },
      { src: '东北抗联2.png', description: '东北烈士纪念馆内油画' }
    ],
