  {
    title: '渭华起义：革命火种点燃陕东大地',
    content: '<p>这里，是较早传播马克思主义的阵地之一。五四运动后，当马克思主义的种子正在中国大地上萌芽生发时，渭南当地由爱国教育家杨松轩创办的咸林中学就相继聘请魏野畴、王复生等来校任教。他们利用教员身份，带领学生阅读《共产党宣言》《新青年》等革命书籍杂志，印刷宣传品，进行马克思主义革命宣传。各地学子纷纷慕名而来，接受新思想、新文化教育。从此，真理的火种播撒在了这片贫瘠土地上，也逐渐在饱受土豪劣绅层层盘剥的人们心中萌动。</p><p>这里，是陕西省建立党、团组织最早的地方之一。1922年8月，王尚德受党组织委派回到家乡创建党团组织，在赤水职业学校建立了陕甘地区最早的社会主义青年团组织，此为团组织在陕甘地区之起源，“赤职”也因此被誉为“陕甘赤化的发祥地”。1927年6月，冯玉祥反共“清党”时，陕西省不少地方的党组织受到破坏，但党在渭华地区的组织基本保存了下来，且有所发展。据中共陕西省委1927年9月26日召开的第一次扩大会议《党务报告》记载，当时全省7个县党委和11个县党支部，其中3个县党委和2个县党支部在渭华地区。</p><p>这里，是大革命时期全省群众运动特别是农民运动最活跃的地方之一。1925年11月22日，张宗适和雷光显等发起成立全省第一个农民协会——东张村乡农民协会，接管本村行政权，成为“陕西农民觉悟的先锋队”。1927年3月，中共陕甘区委提出“党到农民中去”的要求，制定了首先发展渭南、华县（今华州区）、富平、蒲城等县农民运动的计划。1927年5月，华县农协召开群众大会，驱逐了赃官县长叶振本。至1927年5月，渭南县、华县、五一县的县级农民协会均宣告成立并成立农民武装。区、村农协纷纷成立，会员达1.3万人，拥有了起义的群众基础。</p><p>这里，有一支农民革命武装力量。1927年7月，以西安中山军事学校和国民军联军驻陕总司令部政治保卫部领导的政治保卫队为骨干组成了许权中旅，全旅共1290人，枪1020支，有党团员66名，并设有党团委员会，杨晓初任书记。1928年1月，为加强许旅党的工作，中共陕西省委派刘景桂（志丹）、谢浩如（子长）、唐澍等到许旅工作。4月1日陕东特委成立后，为加强渭华地区农民武装的骨干力量，从华县、渭南选派了40名共产党员、共青团员和农民武装骨干分子，秘密前往洛南三要司许权中旅学习军事政治，为渭华地区培养了一支训练有素、纪律严明、富有战斗力的农民革命武装。</p><p>1928年5月1日，渭南县崇凝镇举行纪念“五一”国际劳动节大会，张贴“实行耕者有其田”和“大家吃、大家干、大家的事大家办”等标语，处决两名恶差、抄没恶绅商号，成立陕西第一个苏维埃政权——崇凝区苏维埃政府，举行示威游行，组建陕东赤卫队。</p><p>崇凝区苏维埃政府的成立，标志着渭华起义的开始。它就像一声春雷，炸响在渭华大地，震惊了整个西北地区。随后，华县的赤水、高塘等地和渭南东西两塬各村纷纷行动，焚烧文契账目、缴获枪支弹药，给农民分发粮食和财物，三张民团和崇凝、阳郭区赤卫队以及华县农民武装先后编入陕东赤卫队，并在塔山建立总指挥部。</p><p>5月10日，正在潼关前线参加李虎臣和冯玉祥部作战的许权中旅，在唐澍、刘志丹的率领下，一部分先撤出战斗向渭华地区进发，许权中、杨晓初率部赶到渭华地区，在华县瓜坡正式宣布起义，成立西北工农革命军。部队抵达华县高塘后，在高塘举行军民联欢会，刘继曾代表省委向西北工农革命军授旗，刘志丹任军事委员会主席，唐澍任总司令，王泰吉任参谋长，许权中任军事顾问，下设四个大队和一个直属大队，司令部设在高塘，全军近千人。</p><p>会后举行武装示威游行，张贴布告、公布政纲，号召人民团结起来，打倒土豪劣绅，自己当家作主，建立苏维埃政权。在起义的中心区域内，国民党基层政权全部被摧毁，普遍建立了一村或数村为单位的基层苏维埃政权，清算土豪劣绅、没收地主粮食和浮财、处决罪大恶极者，开办平民学校和农民夜校，壮大革命有生力量。</p><p>在很短的时间里就形成了以华县高塘、渭南塔山为中心，纵横约200平方公里、拥有数十万人口的红色武装割据区域，渭华起义进入高潮。渭华起义的浪潮席卷陕东大地，使驻陕西军阀及其政府大为震惊，他们妄图扑灭农民革命斗争的烈火，多次派遣重兵进行围剿。6月，国民党反动当局先后纠集了三个师的兵力及渭华地区的反动势力向起义中心区域大举进攻，起义军民同仇敌忾，以大无畏的英雄气概，与数十倍之敌进行了艰苦卓绝的战斗，由于敌众我寡，红色区域先后失陷。</p><p>7月初，西北工农革命军遭到军阀李虎臣部围攻，唐澍等起义将领英勇牺牲。向省委汇报工作返回的刘继曾在蓝田县张家坪主持召开会议，初步总结渭华起义失败的经验教训，决定取消西北工农革命军旗帜及军事委员会，党在军队中的组织转入秘密状态，轰轰烈烈的渭华起义宣告失败。1928年后半年，国民党反动派对渭华地区进行了残酷镇压和疯狂反攻，腥风血雨笼罩了渭华地区，先后有300多名共产党员、共青团员和赤卫队队员倒在了敌人的屠刀下。</p>',
    images: [
