  {
    title: '西柏坡：新中国从这里走来，传承“两个务必”精神',
    content: '<p>西柏坡纪念馆，位于河北省平山县西柏坡村，是一座承载着中国革命历史记忆的庄严圣地。这里曾是中共中央所在地，新中国从这里走来。西柏坡是毛主席和党中央进入北平，解放全中国的最后一个农村指挥所，指挥三大战役在此，开党的七届二中全会在此。</p><p>全会规定党在全国胜利后在政治、经济、外交方面应当采取的基本政策，指出中国由农业国转变为工业国、由新民主主义社会转变为社会主义社会的发展方向。</p><p>全会讨论确定了党的工作重心由乡村转移到城市的问题。毛泽东在全会上提出“两个务必”思想，即：“务必使同志们继续地保持谦虚、谨慎、不骄、不躁的作风，务必使同志们继续地保持艰苦奋斗的作风。”</p><p>8月10日，成员到达石家庄市西柏坡纪念馆，参观展厅。首先进入序厅，见到西柏坡中共中央旧址和雄伟巍峨的太行山。在第一展室参观学习了进驻西柏坡前的准备背景，向各个展室走进，学习了解中央工委在西柏坡组织召开全国土地会议、扭转晋察冀军事斗争的局面、组织三大战役、召开七届二中全会、进京赶考……成员学习了“两个务必”精神，感悟革命先辈工作与生活的足迹，更激励我们不忘初心，走好新时代“赶考”路。</p>',
    images: [
      { src: '品读西柏坡1.png', description: '辽沈战役纪念馆' },
      { src: '品读西柏坡2.png', description: '参观平津战役纪念馆' },
      { src: '品读西柏坡3.png', description: '参观西柏坡纪念馆' },
      { src: '品读西柏坡4.png', description: '实践团成员在淮海战役纪念馆合影' },
    ],
   /*  video: 'video-02.mp4', */
    videodescription: '重庆谈判的影像资料',
    source: '18',
    photographer: '1111111111',
    editor: '红色编辑12',
